.goHome_btn {
    background: url(../../images/home.svg);
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    padding: 0;
}

.rotate_btn {
    background: url(../../images/rotate.svg);
    width: 50px;
    height: 50px;
}

.general_btn {
    background: url(../../images/background_1.svg);
    width: 90px;
    height: 50px;
    font-size: 22px;
    text-align: center;
}

.home_btn{
    background: url(../../images/background_2.svg);
    width: 230px;
    height: 50px;
    font-size: 26px;
}

.btn {
    border: none;
    cursor: pointer;
    padding: 3px 10px;
    transition: ease-in-out .2s;
    line-height: 2px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

@media(hover: hover) and (pointer: fine){ 
    .btn {
        &:hover{
            filter: drop-shadow(2px 3px 5px rgba(0, 0, 0, 0.425));
            transform: scale(1.2);
        }
        &:active{
            transition: ease-in-out .1s;
            transform: scale(1);
            filter: drop-shadow(2px 3px 5px rgba(0, 0, 0, 0.425));
        }
    }
}

@media(hover: none) and (pointer: coarse) {
    .btn {
        &:active{
            transition: ease-in-out .1s;
            transform: scale(0.8);
            filter: drop-shadow(2px 3px 5px rgba(0, 0, 0, 0.425));
        }
    }
}

@media(max-width: 1000px) {
    .goHome_btn {
        width: 60px;
        height: 60px;
    }

    @media(max-height: 500px) {
        .goHome_btn{
            width: 35px;
            height: 35px;
        }
    }
}

@media(max-width: 740px) {

    @media(max-height: 360px) {
        .general_btn {
            width: 85px;
            height: 45px;
            font-size: 20px;
        }

        .rotate_btn {
            width: 45px;
            height: 45px;
        }

        .home_btn{
            width: 225px;
            height: 45px;
            font-size: 24px;
        }
    }
}

@media(max-width: 540px) and (orientation: portrait) {
    .goHome_btn{
        width: 35px;
        height: 35px;
    }
}